@import "https://fonts.googleapis.com/css2?family=Sen:wght@400;700&family=Lato:wght@400;700&family=Abril+Fatface&family=Inter:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Merriweather, serif;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

body {
  color: #000;
  background-color: #d1b899;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

nav {
  z-index: 1000;
  background-color: #d1b899;
  width: 100%;
  height: 65px;
  transition: top .3s;
  position: fixed;
  top: 0;
}

.logo a {
  color: #292525;
  padding-left: 20px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
}

.nav__content {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: auto;
  padding: 1rem;
  display: flex;
}

ul {
  place-content: center;
  gap: 1rem;
  padding: .5rem;
  list-style: none;
  transition: left .3s;
  display: flex;
}

ul li a {
  color: #332a2b;
  padding: .5rem 1rem;
  font-size: 15px;
  font-weight: normal;
  transition: all .3s;
}

ul li a:hover {
  color: #e2b67c;
  text-shadow: 0 4px 8px #000;
  font-size: 16px;
}

.bar {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-btn {
  cursor: pointer;
  background: #aa8659;
  border-radius: 0 .75em .75em 0 / 0 1.5em 1.5em 0;
  padding: .75em;
  position: relative;
}

.search-btn span {
  color: #362004;
  display: inline-block;
}

#check {
  display: none;
}

.checkbox {
  color: #fff;
  display: none;
}

.search-container {
  justify-content: center;
  gap: .3rem;
  height: 2rem;
  display: flex;
}

input {
  color: #fff;
  background-color: #d1b899;
  border: none;
  border-radius: 25px;
  width: 100%;
  max-width: 350px;
  padding: 12px;
  font-size: 16px;
  transition: background-color .3s;
  box-shadow: 0 4px 8px #d1b899;
}

input:focus {
  background-color: #a37e50;
  outline: none;
}

input::placeholder {
  color: #362004;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #d1b899;
  border: none;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  font-size: 16px;
  transition: background-color .3s, transform .3s;
  display: flex;
  box-shadow: 0 4px 8px #d1b899;
}

button:hover {
  background-color: #a37e50;
  transform: scale(1.05);
}

button i {
  color: #362004;
  font-size: 20px;
}

footer {
  text-align: center;
  color: #332a2b;
  background-color: #d1b899;
  flex-direction: column;
  padding: 20px;
  font-family: Merriweather, serif;
  display: flex;
}

@media (width <= 768px) {
  .nav__content {
    padding: 1rem;
  }

  nav .checkbox {
    color: #fff;
    font-size: 25px;
    display: block;
  }

  ul {
    background-color: #d1b899;
    flex-direction: column;
    justify-content: center;
    gap: 3rem;
    width: 50%;
    height: calc(100vh - 64px);
    margin: 0;
    padding: 0;
    display: none;
    position: absolute;
    top: 65px;
    right: -100%;
  }

  #check:checked ~ ul {
    display: block;
  }

  #check:checked + .checkbox span:first-child {
    transform: rotate(45deg)translateY(8px);
  }

  #check:checked + .checkbox span:nth-child(2) {
    opacity: 0;
  }

  #check:checked + .checkbox span:nth-child(3) {
    transform: rotate(-45deg)translateY(-8px);
  }

  nav #check:checked ~ ul {
    right: 0;
  }

  ul li {
    text-align: center;
  }

  ul li a {
    font-size: 1rem;
    line-height: 5rem;
  }

  .search-container {
    display: none;
  }
}

@keyframes slideRight {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #aa8659;
}

::-webkit-scrollbar-track {
  background-color: #d1b899;
}
